<template lang="pug">
    .new__test-content
        .new__test-title Тест
        form.new__test
            img(src='../../../assets/images/icons/butterfly.svg').img
            .new__test-points
                .form__row
                    .form__item
                        label.label__name Название
                        input(type='text' v-model='test.code' required).new__test__name
                    .form__item
                        label.label__name Группа
                        select(v-model="test.group.id" required).new__test__group
                            option(value=0) Выбрать
                            option(:value='item.id' v-for='item in teacherGroups') {{item.code}}
                            
                .form__row
                    .form__item.number
                        label.label__name Ответы
                        select(v-model="test.numberAnswers" required).new__test__class
                            option(value=3).option Выбрать
                            option(:value='idx+3' v-for='idx in 3').option {{idx+3}}
                    .form__item
                        label.label__name Время (мин)
                        input(type='number' min='10' max='300' v-model='test.duration' required).new__test__time
                .buttons
                    button(@click.prevent='saveTest').save Сохранить
                    button(@click.prevent='resetTest').reset Отмена
            
</template>
<script>
import { mapState, mapActions } from "vuex";

export default {
    computed:  {
        ...mapState ("teacherGroups", {
            teacherGroups: state => state.teacherGroups})
    },
    created() {
        this.fetchTeacherGroups(this.user.id)
    },
    props: {
        user: Object
    },
    data() {
        return {
            test: {
                code: '',
                group: {
                    id: 0
                },
                duration: 0,
                author: {
                    id: this.user.id
                },
                numberAnswers: 3
            }
        }
    },
    methods: {
        ...mapActions("teacherGroups", ["fetchTeacherGroups"]),
        ...mapActions("tests", ["addTest"]),
        saveTest() {
            if(this.test.code&&this.test.duration&&this.test.group.id&&this.test.numberAnswers!=3){
                this.addTest(this.test)
                this.$emit('eventNewTest')
            }
        },
        resetTest() {
            this.$emit('eventNewTest')
        }
    }
}
</script>
